import React, { useState, useLayoutEffect, useEffect } from "react";

export function UlbiTV_MiniDrADr() {
  const [theme, setTheme] = useState(localStorage.getItem("--theme"));
  const [colorTheme, setolorTheme] = useState("");
  // console.log("theme ", theme);
  // const selColorTheme = () => {
  //   // var colorTheme;
  //   if (theme === "dark") {
  //     // colorTheme = "#f0f";
  //     setolorTheme("#f0f");
  //     console.log("colorTheme 1 ", colorTheme);
  //   }
  //   if (theme === "natural") {
  //     // colorTheme = "#0f0";
  //     setolorTheme("#0f0");
  //     console.log("colorTheme 2 ", colorTheme);
  //   }
  //   if (theme === "light") {
  //     // colorTheme = "#00f";
  //     setolorTheme("#00f");
  //     console.log("colorTheme 3 ", colorTheme);
  //   }
  // };
  // console.log("colorTheme ", colorTheme);
  // useLayoutEffect(() => {
  useEffect(() => {
    // document.body.setAttribute("data-theme", theme);
    // localStorage.setItem("--theme", theme);
    if (theme === "dark") {
      setTheme("dark");
      setolorTheme("#555");
      // setolorTheme("--YellowBrown");
      // console.log("colorTheme 1 ", colorTheme);
    }
    if (theme === "natural") {
      setTheme("natural");
      setolorTheme("#0f0");
      // console.log("colorTheme 2 ", colorTheme);
    }
    if (theme === "light") {
      setTheme("light");
      setolorTheme("#00f");
      // console.log("colorTheme 3 ", colorTheme);
    }
    // }, [theme, colorTheme]);
    // localStorage.setItem("--theme", theme);
  }, [theme, colorTheme]);
  // }
  // return { theme, setTheme };
  // useEffect(() => {
  //   localStorage.getItem("--theme");

  //   // return () => {
  //   //   second
  //   // }
  // }, [theme]);

  //    .  . .  
  const [cardList, setCardList] = useState([
    { id: 1, order: 3, text: " 3" },
    { id: 8, order: 8, text: " 8" },
    { id: 3, order: 2, text: " 2" },
    { id: 7, order: 7, text: " 7" },
    { id: 4, order: 4, text: " 4" },
    { id: 9, order: 9, text: " 9" },
    { id: 5, order: 5, text: " 5" },
    { id: 6, order: 6, text: " 6" },
    { id: 2, order: 1, text: " 1" },
  ]);

  //   
  const [currentCard, setCurrentCard] = useState(null);

  // FN 
  //  
  function dragStartHandler(e, card) {
    // console.log("start ", card);
    //   .   
    setCurrentCard(card);
  }
  //   (- onDragLeave  onDragEnd)
  function dragEndHandler(e) {
    // .    
    e.target.style.backgroundColor = "#333";
    // e.target.style.backgroundColor = {'{--DarkRed} ? {--DarkRed} : "white"'};
  }
  //   . 
  function dragOverHandler(e) {
    // . .  .
    e.preventDefault();
    //   
    e.target.style.backgroundColor = "lightgray";
    e.target.style.backgroundColor = colorTheme ? colorTheme : "gray";
    // e.target.style.setProperty("backgroundColor", "--footer-color");
    // e.target.style.setProperty("--footer-color", "backgroundColor");
  }
  // .   ( )
  function dropHandler(e, card) {
    // console.log("drop ", card);
    // . .  .
    e.preventDefault();
    //    .       
    setCardList(
      cardList.map((c) => {
        // . / .id . = id  
        if (c.id === card.id) {
          //  .       
          return { ...c, order: currentCard.order };
        }
        // . / .id . = id  
        if (c.id === currentCard.id) {
          //  . u      
          return { ...c, order: card.order };
        }
        return c;
      })
    );
    //    .
    e.target.style.backgroundColor = "white";
    e.target.style.backgroundColor = "#333";
  }

  // fn   2 
  const sortCard = (a, d) => {
    // / 1 > 2  . 1
    if (a.order > d.order) return 1;
    //  . -1
    else return -1;
  };

  return (
    <div className="UlbiTV_MiniDrADr">
      <div className="MiniDrADr__descript">
        <h1>MiniDrADr</h1>
      </div>
      <div className="MiniDrADr__content">
        {/*  .  .   map  sort   fn() */}
        {cardList.sort(sortCard).map((card) => (
          <div
            key={card.id}
            className={`card`}
            // .    . fn . .  - Start  Drop   
            //     true
            draggable={true}
            //   .  
            onDragStart={(e) => dragStartHandler(e, card)}
            // .     . 
            onDragLeave={(e) => dragEndHandler(e)}
            // .  . 
            onDragEnd={(e) => dragEndHandler(e)}
            // .  .  . .
            onDragOver={(e) => dragOverHandler(e)}
            //    . .
            onDrop={(e) => dropHandler(e, card)}
          >
            {card.text}
          </div>
        ))}
      </div>
    </div>
  );
}
// export default UlbiTV_MiniDrADr;
