import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";

import { ArrowAccordionFnComp } from "../../../../../Components/ui/accordion/ArrowAccordion.jsx";

import "./FormMN.scss";

const FormMN = () => {
  const [openArrowAccord, setOpenArrowAccord] = useState(false);
  const handleClickRef = () => {
    setOpenArrowAccord(!openArrowAccord);
  };

  //     useForm
  const {
    //   
    //   
    register,
    //   -
    formState: {
      errors,
      //  . 
      isValid,
    },
    //    
    handleSubmit,
    //  
    reset,
  } = useForm({
    //  moda all/onBlur()/onCgange/onSubmit/...
    mode: "onBlur",
  });
  // = useForm();
  //      

  //  handle ().  .  form /  handleSubmit
  const onSubmit = (data) => {
    alert(JSON.stringify(data) + "  .  useForm. .!");
    //  
    reset();
  };

  return (
    <div className="FormMN accordion">
      <div className="FormMN__descript">
        <h1
          className={openArrowAccord ? "_active" : ""}
          onClick={() => {
            handleClickRef();
          }}
        >
          FormMN
        </h1>
        <div className={openArrowAccord ? "openDop" : ""}>
          <p>
                <span>useForm</span>
          </p>
          {/* <ol>
            <li>
                  useForm
              <ul>
                <li>npm i react-modal</li>
                <li>npm i styled-components @types/styled-components</li>
              </ul>
            </li>
          </ol> */}
          <p style={{ color: "red", backgroundColor: "#111" }}>
            !!!   ././  . -
            https://react-hook-form.com
          </p>
        </div>
        <ArrowAccordionFnComp
          openArrowAccord={openArrowAccord}
          setOpenArrowAccord={setOpenArrowAccord}
        />
      </div>
      <div className="FormMN__content openCont">
        {/* .   onSubmit   . handleSubmit */}
        <form onSubmit={handleSubmit(onSubmit)}>
          {/* input 1.    */}
          <label>
            :
            <input
              placeholder=""
              //   ( name, value, onChange, ...)
              {
                //  useForm .,  . .  2  - 1 .  (name,   data), 2  
                ...register("firstName", {
                  //   ()
                  //   (message) || boolean // !      input     
                  required: "   ",
                  //  .  .     .
                  minLength: {
                    value: 3,
                    message: " 3 ",
                  },
                })
              }
            />
            {/*   . /  .   firstName( register)   <p>  .  .  <p>    massage,   . */}
            {errors?.firstName && (
              <p>{errors?.firstName?.message || "Error!  firstName"}</p>
            )}
          </label>
          {/*   .   label,   div */}
          {/* <div style={{ height: 40 }}> */}
          {/* {errors?.firstName && <p>{errors?.firstName?.message || "Error!  firstName"}</ p>} */}
          {/* </div> */}
          {/* input 2 */}
          <label>
            :
            <input
              {...register("lastName", {
                required: " 2   ",
                minLength: {
                  value: 3,
                  message: " 2. 5 ",
                },
              })}
            />
            {errors?.lastName && (
              <p>{errors?.lastName?.message || "Error!  lastName"}</p>
            )}
          </label>
          {/* input 3 */}
          <label>
            input 3:
            <input
              {...register("lastName3", {
                required: " 2   ",
                minLength: {
                  value: 3,
                  message: " 2. 5 ",
                },
              })}
            />
            {errors?.lastName && (
              <p>{errors?.lastName?.message || "Error!  lastName"}</p>
            )}
          </label>
          {/* input 4 */}
          <label>
            input 4:
            <input
              {...register("lastName4", {
                required: " 2   ",
                minLength: {
                  value: 3,
                  message: " 2. 5 ",
                },
              })}
            />
            {errors?.lastName && (
              <p>{errors?.lastName?.message || "Error!  lastName"}</p>
            )}
          </label>
          {/* .  */}
          <input
            className="primary"
            type="submit"
            //    .
            disabled={!isValid}
          />
        </form>
      </div>
    </div>
  );
};
export { FormMN };
