import React, { useState } from "react";

const questions = [
  {
    id: 1,
    title: "React -  ... ?",
    variants: ["", "", ""],
    correct: 0,
  },
  {
    id: 2,
    title: " -  ... ",
    variants: [
      "",
      "   ",
      ",      ",
    ],
    correct: 1,
  },
  {
    id: 3,
    title: "  JSX?",
    variants: [
      "  HTML",
      " ",
      "   HTML,     JS-",
    ],
    correct: 2,
  },
  {
    id: 4,
    title: "React -  ... ?",
    variants: ["", "", ""],
    correct: 0,
  },
  {
    id: 5,
    title: " -  ... ",
    variants: [
      "",
      "   ",
      ",      ",
    ],
    correct: 1,
  },
  {
    id: 6,
    title: "  JSX?",
    variants: [
      "  HTML",
      " ",
      "   HTML,     JS-",
    ],
    correct: 2,
  },
];

function Result({ correct }) {
  return (
    <div className="result">
      <img
        alt=""
        src="https://cdn-icons-png.flaticon.com/512/2278/2278992.png"
      />
      {/* -    */}
      <h2>
          {correct}   {questions.length}
      </h2>
      {/*     . */}
      <a href="/BeginrProjts">
        <button> </button>
      </a>
    </div>
  );
}

function Game({ question, onClickVar, step }) {
  //  %  / , -   
  const percentTage = Math.round((step / questions.length) * 100);
  // console.log('percentTage : ' + percentTage);

  return (
    <>
      <div className="progress">
        <div
          //     %
          style={{ width: `${percentTage}%` }}
          className="progress__inner"
        ></div>
      </div>
      {/*   */}
      <h1>{question.title}</h1>
      <ul>
        {/*  . */}
        {/*    li  fn() .  id/index  .  . */}
        {question.variants.map((varik, id) => (
          <li
            // onClick={onClickVar}
            onClick={() => onClickVar(id)}
            key={id}
          >
            {varik}
          </li>
        ))}
      </ul>
    </>
  );
}

export function BegPrj_Quiz() {
  //  /index 
  const [step, setStep] = useState(0);
  //  .  .   step
  const question = questions[step];
  // .    
  const [correct, setCorrect] = useState(0);

  //    
  const onClickVar = (id) => {
    console.log(step, id, question.correct);
    //    1
    setStep(step + 1);

    //        
    if (id === question.correct) {
      setCorrect(correct + 1);
    }
  };

  return (
    <div className="BegPrj_Quiz">
      <div className="Quiz__descript">
        <h1>BegPrj_Quiz</h1>
      </div>
      <div className="Quiz__content">
        {/*      */}
        {/* fn .       */}
        {/*      */}
        {/*     - . /  ,   */}
        {step !== questions.length ? (
          <Game question={question} onClickVar={onClickVar} step={step} />
        ) : (
          //  - ..
          <Result correct={correct} />
        )}
      </div>
    </div>
  );
}
